.pagination-wrap {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 21px;
  &.left {
    justify-content: flex-start;
    .pagination {
      li {
        a,
        span {
          background-color: #f8f8f8; } } } }
  u {
    display: inline-block;
    font-size: 20px;
    line-height: 24px;
    margin-right: 52px;
    color: #555555; }
  .pagination {
    margin-left: -4px;
    margin-right: -4px;
    li {
      display: inline-block;
      vertical-align: middle;
      padding: 0 4px;
      a,
      span {
        color: #85878A;
        display: block;
        width: 32px;
        font-size: 14px;
        line-height: 32px;
        text-align: center;
        border-radius: 5px; }
      &.disable {
        a,
        span {
          opacity: .4;
          pointer-event: none; } }
      &.active,
      &:hover {
        a,
        span {
          color: #fff;
          background: #6BBB71; } }
      &.before,
      &.after {
        span,
        a {
          width: 76px;
          height: 32px;
          display: flex;
          background: #E8E8E8;
          border-radius: 5px;
          text-align: center;
          transition: all .5s ease;
          .ic-arrow {
            width: 16px;
            height: 14px;
            display: inline-block;
            margin: auto;
            background: url(../images/ic-arrow-right-grey.svg);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center; } }
        &:hover {
          a,
          span {
            background-color: #6BBB71;
            .ic-arrow {
              background: url(../images/ic-arrow-right-white.svg);
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center; } } } }
      &.before {
        span,
        a {
          .ic-arrow {
            transform: rotate(-180deg); } } } } }

  .text {
    margin-right: 15px;
    p {
      text-align: left;
      margin-bottom: 0;
      font-size: 21px;
      line-height: 24px;
      color: #555; } } }

